// 支付结果
import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';

import {PayLibrary} from 'app/core/libs/pay-library';
import {ErrorService} from 'app/core/services/error.service';
import {LoadingService} from 'app/core/services/loading.service';
import {LogService} from 'app/core/services/log.service';

import {UserService} from 'app/core/services/user.service';
import {InfoMessage} from 'app/core/objects/info-message';
import {OutputQueryInHospitalPatient} from 'app/protocol/server/output-query-in-hospital-patient';
import {OutputQueryOutPatientInfo} from 'app/protocol/server/output-query-out-patient-info';
import {InfoLibrary} from 'app/core/libs/info-library';
import {SystemService} from 'app/core/services/system.service';

@Component({
  selector: 'app-payment-result',
  templateUrl: './payment-result.component.html',
  styleUrls: ['./payment-result.component.css']
})
export class PaymentResultComponent implements OnInit {
  // 门诊信息
  outpatientInfo: OutputQueryOutPatientInfo;
  // 住院信息
  inpatientInfo: OutputQueryInHospitalPatient;

  tips: string // 显示的文字（充值成功，支付成功）

  paymentType: string;
  orderStatus: string;
  orderAmount: string;
  paymentResult: string;
  dataSrc: string;

  constructor(private route: ActivatedRoute,
              private userService: UserService,
              private errorService: ErrorService,
              private systemService: SystemService,
              private loadingService: LoadingService) {
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.paymentType = params['paymentType'];
      this.orderStatus = params['orderStatus'];
      this.orderAmount = params['orderAmount'];
      this.dataSrc = params['dataSrc'];

      if (this.orderStatus === PayLibrary.orderStatusSucToHIS) {
        this.paymentResult = PayLibrary.PaySuccess;

        if (this.dataSrc === PayLibrary.dataSrcInpatient) {
          this.inpatientInfo = this.userService.getInPatientInfo();
          this.getInpatientInfo(this.inpatientInfo.serial_number);
        } else {
          this.outpatientInfo = this.userService.getOutPatientInfo();
          this.getPatientInfo(this.outpatientInfo.patient_card);
        }
      } else if (this.orderStatus === PayLibrary.orderStatusFailedToHIS) {
        this.paymentResult = PayLibrary.FailedToHIS;
      } else {
        this.paymentResult = PayLibrary.PayFail;
      }

      // 显示文字
      if (this.dataSrc === PayLibrary.dataSrcHIS) {
        this.tips = InfoLibrary.tipsPaySuc; // 支付成功
      } else {
        this.tips = InfoLibrary.tipsDepositSuc; // 充值成功
      }

    });
  }

  // 从服务器获取用户信息
  private getPatientInfo(inpatientNum: string): void {
    this.loadingService.showLoading();

    this.userService.getOutPatientInfoFromServer(inpatientNum).subscribe(
      data => this.getPatientInfoSuc(data),
      error => this.getPatientInfoErr(error)
    );
  }

  // 从服务器获取用户信息，成功
  private getPatientInfoSuc(outputQueryOutPatientInfo: OutputQueryOutPatientInfo): void {
    LogService.debug('getPatientInfoSuc: ' + outputQueryOutPatientInfo);

    this.outpatientInfo = outputQueryOutPatientInfo;

    this.loadingService.hiddenLoading();
  }

  // 从服务器获取用户信息，失败
  private getPatientInfoErr(errorMsg: string): void {
    LogService.error('getPatientInfoErr: ' + errorMsg);
    this.loadingService.hiddenLoading();

    if (errorMsg !== InfoLibrary.networkError) {
      // 普通错误
      const infoMessage: InfoMessage = new InfoMessage(InfoLibrary.outpatientNumError);
      this.errorService.showError(infoMessage);
    } else {
      // 网络错误，跳去指定UI
      this.systemService.gotoErrorWait();
    }
  }

  // 从服务器获取住院病人信息
  private getInpatientInfo(serialNumber: string): void {
    this.loadingService.showLoading();

    this.userService.getInpatientInfoFromServer(serialNumber).subscribe(
      data => this.getInpatientInfoSuc(data),
      error => this.getInpatientInfoErr(error)
    );
  }

  // 从服务器获取住院病人信息，成功
  private getInpatientInfoSuc(outputQueryInHospitalPatient: OutputQueryInHospitalPatient): void {
    LogService.debug('getInpatientInfoSuc: ' + outputQueryInHospitalPatient);
    this.inpatientInfo = outputQueryInHospitalPatient;
    this.loadingService.hiddenLoading();
  }

  // 从服务器获取住院病人信息，失败
  private getInpatientInfoErr(errorMsg: string): void {
    LogService.error('getInpatientInfoErr: ' + errorMsg);
    this.loadingService.hiddenLoading();

    if (errorMsg !== InfoLibrary.networkError) {
      // 普通错误
      const infoMessage: InfoMessage = new InfoMessage(InfoLibrary.inpatientNumError);
      this.errorService.showError(infoMessage);
    } else {
      // 网络错误，跳去指定UI
      this.systemService.gotoErrorWait();
    }
  }
}



