// 模块内的路由设置

import {ModuleWithProviders} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import {PathLibrary} from 'app/core/libs/path-library';
import {OutpatientBillingComponent} from 'app/c-hnxrmyy/outpatient-billing/outpatient-billing.component';
import {PaymentResultComponent} from 'app/c-hnxrmyy/payment-result/payment-result.component';

const routes: Routes = [
  {path: PathLibrary.outpatientBilling, component: OutpatientBillingComponent},
  {path: PathLibrary.paymentResult, component: PaymentResultComponent}
];

export const HnxrmyyRouter: ModuleWithProviders = RouterModule.forChild(routes);



