// 门诊缴费记录

import {Component} from '@angular/core';

import {PathLibrary} from 'app/core/libs/path-library';
import {PayInfo} from 'app/core/objects/pay-info';
import {OutpatientBillingBaseComponent} from 'app/base-component/outpatient-billing-base/outpatient-billing-base.component';
import {ProxyLibrary} from 'app/core/libs/proxy-library';

@Component({
  selector: 'app-outpatient-billing',
  templateUrl: './outpatient-billing.component.html',
  styleUrls: ['./outpatient-billing.component.css']
})
export class OutpatientBillingComponent extends OutpatientBillingBaseComponent {

  // 跳转支付类型选择UI
  protected goToPaymentUI(): void {
    const payInfo: PayInfo = new PayInfo();
    payInfo.dataSrc = ProxyLibrary.getOutpatientPayDataSrc();
    payInfo.totalAmount = this.billAmount;
    payInfo.visitID = this.selectedRecord.outpatientPayRecordsListItem.his_id;

    // 跳向 支付类型选择UI
    this.routerService.goTo([PathLibrary.paymentTypeSelect, payInfo]);
  }

}



